<template>
  <div class="allPage">
    <div class="core_container">
      <div class="form_container">
        <el-form
          :inline="true"
          class="demo-form-inline form_container_header"
          size="mini"
        >
          <el-form-item label="搜索项">
            <el-select v-model="requestParam.search.item" placeholder="请选择">
              <el-option lable="用户Id" value="uid"></el-option>
              <el-option lable="分组" value="room" :disabled="true"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="搜索内容">
            <el-input v-model.number="requestParam.search.value"></el-input>
          </el-form-item>
          <el-form-item label="刷选类别">
            <template>
              <el-select
                v-model="requestParam.search_item"
                placeholder="请选择"
              >
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </template>
          </el-form-item>
          <el-form-item label="金额范围">
            <el-input
              v-model.number="requestParam.search.range[0]"
              placeholder="最低金额"
              size="small"
              class="minInput"
            ></el-input>
            -
            <el-input
              v-model.number="requestParam.search.range[1]"
              placeholder="最高金额"
              size="small"
              class="minInput"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getList">查询</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="showDataTab">
        <el-table
          :data="page.list"
          style="width: 100%"
          :row-class-name="tableRowClassName"
        >
          <el-table-column prop="total.uid" label="用户ID" align="center">
          </el-table-column>
          <el-table-column prop="nick_name" label="昵称" align="center">
          </el-table-column>
          <el-table-column prop="total.group" label="玩家分组" align="center">
          </el-table-column>
          <el-table-column
            prop="total.total_gold"
            label="总金币"
            align="center"
          >
          </el-table-column>
          <el-table-column prop="total.recharge" label="总充值" align="center">
          </el-table-column>
          <el-table-column prop="total.winLose" label="总输赢" align="center">
          </el-table-column>
          <el-table-column prop="total.flow" label="总流水" align="center">
          </el-table-column>

          <el-table-column
            prop="day.recharge_diff"
            label="今日充值"
            align="center"
          >
          </el-table-column>
          <el-table-column
            prop="day.winLose_diff"
            label="今日输赢"
            align="center"
          >
          </el-table-column>
          <el-table-column prop="day.flow_diff" label="今日流水" align="center">
              <template slot-scope="scope">
                  {{scope.row.day.flow_diff|returnDefault}}
              </template>
          </el-table-column>
          <el-table-column prop="day.tax_diff" label="今日税收" align="center">
              <template slot-scope="scope">
                  {{scope.row.day.tax_diff|returnDefault}}
              </template>
          </el-table-column>
          <el-table-column prop="total.uc" label="后台调整" align="center">
            <template slot-scope="scope">
              <el-popover placement="right" width="400" trigger="click">
                <ul>
                  <li>
                    受控:{{
                      scope.row.total.uc.c == null ||
                      scope.row.total.uc.c == false
                        ? "否"
                        : "是"
                    }}
                  </li>
                  <li>控制目标:{{ scope.row.total.uc.tar }}</li>
                  <li>控制等级:{{ scope.row.total.uc.level }}</li>
                  <li>控制进度:{{ scope.row.total.uc.now }}</li>
                  <li>
                    完成目标:{{
                      scope.row.total.uc.achieveTarget == null ||
                      scope.row.total.uc.achieveTarget == false
                        ? "否"
                        : "是"
                    }}
                  </li>
                </ul>
                <el-button slot="reference">展示</el-button>
              </el-popover>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="pageBar">
        <el-pagination
          background
          hide-on-single-page
          layout="prev, pager, next"
          @next-click="nextPage"
          @current-change="thisPage"
          @prev-click="prePage"
          :current-page="page.current_page"
          :page-size="page.page_size"
          :total="page.total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";
export default {
  computed: {},
  data() {
    return {
      labelPosition: "right",
      requestParam: {
        search: {
          item: "uid",
          value: null,
          range_obj: "total_gold",
          range: [],
          sort: 1
        }
      },
      page: {
        list: [],
        current_page: 1,
        total: 0,
        page_size: 8
      },
      options: [
        {
          value: "total_gold",
          label: "总金币"
        },
        {
          value: "winLose",
          label: "输赢"
        },
        {
          value: "flow",
          label: "流水"
        },
        {
          value: "reward",
          label: "奖励"
        },
        {
          value: "give",
          label: "赠与"
        },
        {
          value: "receive",
          label: "收到"
        }
      ]
    };
  },
  methods: {
    getList() {
      var that = this;
      that.page.list = [];
      that.page.total = 0;
      let query = {
        search: this.requestParam.search,
        page: {
          current_page: this.page.current_page,
          page_size: this.page.page_size
        }
      };
      this.$api.statistic.list_realTimeData(query).then(res => {
        if (res.data.code === 200) {
          that.page.total = res.data.data.total;
          that.page.list = res.data.data.list;
        }
      });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 4 === 1) {
        return "warning-row";
      } else if (rowIndex % 4 === 3) {
        return "success-row";
      }
      return "";
    },
    prePage() {
      if (this.page.current_page > 0) {
        this.page.current_page--;
        this.getList();
      }
    },
    thisPage(cpage) {
      this.page.current_page = cpage;
      this.getList();
    },
    nextPage() {
      if (
        this.page.current_page <
        Math.ceil(this.page.total / this.page.page_size)
      ) {
        this.page.current_page++;
        this.getList();
      }
    }
  },
  filters: {
    formatDate: function(value) {
      return moment(value).format("YYYY/MM/DD hh:mm:ss a");
    },
    returnDefault:function(value){
        if(!value){
            return 0;
        }
    }
  }
};
</script>

<style lang="less" scoped>
.pageBar {
  width: 100%;
  height: 100px;
  // background-color: aquamarine;
  display: flex;
  justify-content: center;
  align-items: center;
}
.innerForm {
  padding: 2% 5%;
  border: #eef1f6 1px solid;
  margin-left: -100px;
  //box-shadow: #eef1f6 0px 0px 10px;
}
.innerForm_item {
  margin: 10px 0;
}
.dialog_input_small {
  width: 35%;
  margin: 0 20px;
}
.minInput {
  width: 40%;
}
.greenCor {
  font-size: larger;
  color: green;
}
.redCor {
  color: red;
}
</style>



